/* Normal Image */
.img {
  object-fit: cover;
  height: 15rem;
  width: 15rem;
  border-radius: var(--border-radius-3);
}

/* Rounded Image */
.img.img-round {
  border-radius: var(--border-rounded);
}

/* Responsive Image */
.img.img-responsive {
  width: 100%;
  height: auto;
}
